import uniLoadMore from '@/colorui/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			page: 1,
			pageSize: 10,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			TabCur: 0,
			buildings: [],
			rooms: [],
			para: {
				start_time: "",
				end_time: "",
				meeting_id: 0,
				room_number: "",
				building_name: ""
			}
		}
	},
	onLoad(option) {
		var info = JSON.parse(decodeURIComponent(option.para));
		this.para = info;
		//获取会议室所属办公楼房信息
		global.$http.post('/meeting/info/buildingList', {
			params: {
				page: 1,
				pageSize: 1000
			},
		}).then(res => {
			if (res.status === "0") {
				this.buildings = res.data;
				this.loadData();
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	onReachBottom() {
		if (this.status !== "noMore") {
			this.status = 'more';
			this.loadData();
		}
	},
	methods: {
		loadData(){
			if(this.buildings.length > 0){
				this.status = 'loading';
				global.$http.post('/meeting/info/meetingList', {
					params: {
						page: this.page,
						pageSize: this.pageSize,
						building_id: this.buildings[this.TabCur].id
					},
				}).then(res => {
					if (res.status === "0") {
						if (res.data.totlePage <= this.page) {
							this.status = 'noMore';
						} else {
							this.status = "more";
						}
						if (this.page === 1) {
							this.rooms = res.data.list;
						} else {
							res.data.list.forEach(c => {
								this.rooms.push(c);
							});
						}
						this.page++;
					} else {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						});
					}
				}).catch(err => {
					uni.showToast({
						title: err.message,
						icon: 'none'
					});
				});
			}
		},
		buildingTab: function(index) {
			this.TabCur = index;
			this.page = 1;
			this.rooms = [];
			this.loadData();
		},
		roomDetail: function(room){
			this.para.meeting_id = room.id;
			this.para.room_number = room.number;
			this.para.building_name = room.name;
			uni.navigateTo({
				url: '../meeting/bookMeeting?para=' + encodeURIComponent(JSON.stringify(this.para))
			});
		}
	}
}
